<template>
    <div class="cs-main">
        <div class="head-top">
            <compass-header title=""></compass-header>
        </div>
        <div class="select-list">
            <ul>
                <a href=""><li><span class="fright">{{ query.country }}    &nbsp  ></span></li></a>
                <a href="#"><li><span class="fright">{{ query.majoy }}&nbsp  ></span></li></a>
                <a href="#"><li><span class="fright">{{ query.clgType }}&nbsp  ></span></li></a>
                <a href="#"><li><span class="fright">{{ query.array }}&nbsp  ></span></li></a>
            </ul>
        </div>
    </div>
</template>
<script>
import CompassHeader from '../components/CompassHeader'
export default {
    data() {
            return {
                query: {
                    country: '',
                    province: '',
                    city: '',
                    majoy: '',
                    clgType: '',
                    array: '9-20',
                }
            }
        },

        components: {
            CompassHeader
        }
}
</script>
<style>
.cs-main {
    height: 3rem;
    background-color: #fff;
}

.head-top {
    height: 1rem;
    border-bottom: 1px solid #efefef;
}

.select-list ul li {
    height: 50px;
    line-height: 50px;
    border-bottom: 1px solid #efefef;
    padding: 0 .08rem;
    font-size: .14rem;
    /* float: left; */
}

</style>



